@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
li.dev-item {
    background-color: #fff;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 3px;
    padding: 20px;
}

li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.dev-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header div {
    margin-left: 10px;
}

li.dev-item header div strong {
    display: block;
    font-size: 16px;
    color: #333;
}

li.dev-item header div span {
    font-size: 13px;
    color: #999;
    margin-top: 2px;
}

li.dev-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.dev-item a:hover {
    color: #5a2ea6;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root {
    height: 100%;
}

body {
    background-color: #E5E6F0;
    -webkit-font-smoothing: antialiased;
}

body, input, button {
    font-family: Roboto, sans-serif;
}
#app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

#app main {
  margin-left: 20px;
}

@media (max-width: 1000px) {

  #app {
    flex-direction: column;
  }

  #app main {
    margin-left: 0;
    margin-top: 20px;
  }

  #app aside {
    width: 100%;
  }
}
aside {
    width: 320px;
    background-color: #FFF;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 3px;
    padding: 30px;
}

aside strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

aside form  {
    margin-top: 30px;
}

aside form .input-group {
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

aside form .input-group .input-block {
    margin-top: 0 !important;
}

aside form .input-block + .input-block {
    margin-top: 20px;
}

aside form .input-block label {
    color: #acacac;
    font-size: 14px;
    font-weight: bold;
    display: block;
}

aside form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #eee;
}

aside form button {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background-color: #7d40e7;
    border-radius: 3px;
    padding: 15px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
}

aside form button:hover {
    background-color: #6931ca;
}
main {
    flex: 1 1;
}

main ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 650px) {

    main ul {
        grid-template-columns: 1fr;
    }
}
